import React, { useState } from "react";
export default function TextForm(props) {
  const handleUpClick = () => {
    // console.log("Uppercase was clicked "+text);
    let newText1 = text.toUpperCase();
    setText(newText1);
    props.showAlert('Converted to UpperCase','success');
  };
  const handleDownClick = () => {
    // console.log("Uppercase was clicked "+text);
    let newText2 = text.toLowerCase();
    setText(newText2);
    props.showAlert('Converted to LowerCase','success');
  };
  const handleOnChange = (event) => {
    // console.log("On Change");
    setText(event.target.value);
  };
  const handleOnReverse = () => {
    let newText3 = text.split("").reverse().join("");
    //with loops
    // let newText3 = "";
    // for (var i = text.length - 1; i >= 0; i--) {
    //     newText3 += text[i];
    // }
    setText(newText3);
    props.showAlert('String Reversed','success');
  };
  const handleOnRemoveSpaces = () => {
    let newText5 = text.split(/[ ]+/);
    setText(newText5.join(" "));
    props.showAlert('Extra space removed','success');
  };
  const handleOnClear = () => {
    let newText4 = "";
    setText(newText4);
    props.showAlert('Cleared!!','success');
  };
  const handleOnCopy = () => {
    let text = document.getElementById("myBox");
    text.select();
    navigator.clipboard.writeText(text.value);
    props.showAlert('Copied!!','success');
  };

  const [text, setText] = useState("");
  return (
    <>
      <div className="container">
        {/* style={myStyle} */}
        <h1>{props.heading}</h1>
        <textarea
          className="form-control"
          value={text}
          onChange={handleOnChange}
          id="myBox"
          rows="8"
          style={{backgroundColor : props.mode==='dark'?'#000000':'white', color : props.mode==='dark'?'white':'#000000'}}
        ></textarea>
        <button className="btn btn-primary my-3" onClick={handleUpClick}>
          Convert to UpperCase
        </button>
        <button className="btn btn-primary my-3 mx-2" onClick={handleDownClick}>
          Convert to LowerCase
        </button>
        <button className="btn btn-primary my-3 mx-2" onClick={handleOnReverse}>
          Reverse
        </button>
        <button
          className="btn btn-primary my-3 mx-2"
          onClick={handleOnRemoveSpaces}
        >
          Remove Extra Spaces
        </button>
        <button className="btn btn-primary my-3 mx-2" onClick={handleOnClear}>
          Clear Text
        </button>
        <button className="btn btn-primary my-3 mx-2" onClick={handleOnCopy}>
          Copy Text
        </button>
        {/* <button className="btn btn-primary my-3 mx-2" >
          Paste Text
        </button> */}
        {/* <button className="btn btn-primary my-3 mx-2" onClick={enableDarkMode}>{btnText}</button> */}
      </div>
      <div className="conatiner my-3">
        {/* style={myStyle} */}
        <h2>Preview</h2><hr />
        <p>{text.length>0?text:"Enter something in the above box"}</p><hr />
        <h2> Your Text Summary</h2>
        <p>
          {text.split(" ").length - 1} words and {text.length} characters
        </p>
        <p>{(text.split(" ").length - 1) * 0.008} Minutes to read</p><br />
      </div>
    </>
  );
}
